import React, { Component } from 'react';

// import {Link} from 'react-router-dom'
import { Carousel, WingBlank, WhiteSpace } from 'antd-mobile';
import Foot from '../../components/foot'
import Head from '../../components/head'
import { get_car ,addNum,descNum,changeTotal,showData,abc} from '../../actions'
import { connect } from "react-redux"
import url from "url";
import axios from "axios";
axios.defaults.baseURL = "http://47.98.135.91:4000/";

@connect(
    state => (
        {
            carlist: state.carlist,
            checkboxCount:state.checkboxCount,
            a:state.a,
            show:state.show
        }
    )
)
export default class Car extends Component {
    loadcar=()=>{
        const { dispatch} = this.props;
        dispatch(get_car("/react/car"))
    }
    changeTotal=()=>{
        const {carlist, checkboxCount,dispatch,a} = this.props;
        var checkOne=document.getElementsByClassName('a')
                var total=0;
                for(var i=0,count=0;i<checkOne.length;i++){
                    if(checkOne[i].checked){
                        count++
                        var totalPrice=document.getElementsByClassName('totalPrice')
                        console.log(totalPrice)
                        for(var j=0;j<totalPrice.length;j++){
                            var subTotalPrice=totalPrice[i].innerHTML*1
                            total=subTotalPrice/checkOne.length+total
                            console.log(totalPrice[i])
                        }
                    }
                }
                dispatch(changeTotal(total.toFixed(2)))
    }
    componentWillMount() {
       this.loadcar();
    }
    oncheck=()=>{
        const {carlist, checkboxCount,dispatch,a} = this.props;
    
        var checkOne=document.getElementsByClassName('a')
        this.changeTotal()

        for(var i=0,count=0;i<checkOne.length;i++){
            if(checkOne[i].checked){
                count++
            }
        }
        var quan=document.getElementById('quan')
        console.log(count)
        if(count==carlist.length){
            quan.checked=true
        }else{
            quan.checked=false
        }
    }
    checkAll=()=>{
     
        var total=0;
        var quan=document.getElementById('quan')
        var checkOne=document.getElementsByClassName('a')
        const status=quan.checked;
        for(var i=0;i<checkOne.length;i++){
            checkOne[i].checked=status;
        }
        total=status?checkOne.length:0;
        console.log(checkOne.length)
        console.log(total)
        this.changeTotal()
    }
    abc=(num)=>{
        const {dispatch,a} = this.props;
        dispatch(abc(num));
    }
    addCount = (num,id) => {
        const {dispatch,a} = this.props;
        num++;
        var that=this;
        dispatch(addNum(num,id));
        
    }
    componentDidUpdate(){
        this.changeTotal()
    }

    descCount = (num,id) => {
        if(num==1){
            return false;
        }
        num--;
        var that=this;
      const{dispatch}=this.props;
      dispatch(descNum(num,id));
    }
    show=()=>{
        const { dispatch,show} = this.props;
        const goumai=this.refs.goumai;
        const shanchu=this.refs.shanchu;
        dispatch(showData(1));
        if(show==1){
            goumai.style.display="block"
            shanchu.style.display="none"
        }else{
            goumai.style.display="none"
            shanchu.style.display="block"
            shanchu.style.backgroundColor="red"
        }
      
    }
    deleteAll=()=>{
        var checkOne=document.getElementsByClassName('a')
        for(var i=0;i<checkOne.length;i++){
            if(checkOne[i].checked){
                var data=checkOne[i].getAttribute("data-id");
                var tr=checkOne[i].parentNode.parentNode;
                console.log(tr)
                tr.parentNode.removeChild(tr);
                i--
                console.log(data)
                axios.get('/react/delete',{
                    params:{id:data}
                })
            }
        }
        this.changeTotal()
    }
    goback=()=>{
        const {history} = this.props;
        history.goBack()
    }
    render(){
        const { dispatch,carlist,a} = this.props;
        return (
            <div id="car">
            <div className="header">
                <div className='back'>
                    <i className='iconfont icon-zuojiantou1' onClick={this.goback}></i>
                </div>
                <div onClick={this.show} className='redact'>
                    编辑
                </div>
            </div>
            <h6></h6>
                <table>
                    <tbody>
                    {
                        carlist.map((item, idx) => (
                        <tr key={idx}>
                            <td><input type="checkbox" ref='one' onClick={()=>this.oncheck()}  className='a'  data-id={item.id}/></td>
                            <td><img  src={item.icon} alt=""/></td>
                            <td className="total">
                                <p>{item.name}</p>
                                <p className="price">{item.price}</p>
                                <p>
                                    <span onClick={()=>{this.descCount(item.num,item.id)}}>-</span>
                                    <b className='totalPrice' style={{ display: 'none'}}>{item.price*item.num}</b>
                                    <input type="number" value={item.num} onChange={()=>{}}/>
                                    <span onClick={()=>{this.addCount(item.num,item.id)}}>+</span>
                                </p>
                            </td>
                        </tr>
                             ))
                            }
                    </tbody>
                </table>
                <div className="footer">
                    <div className="checkAll">
                        <input type="checkbox" id="quan" onClick={this.checkAll}/>
                        <label>全选</label>
                    </div> 
                    <h1 id="total">总计:￥{a}</h1>
                    <div className="buy" ref="goumai">
                        购买
                    </div>
                    <div onClick={this.deleteAll} className="buy" style={{display:'none'}} ref='shanchu'>
                            删除
                    </div>
                </div>
               
            </div> 
        )
    }
    
}



