import React,{Component} from 'react';
import { List, InputItem, WingBlank,WhiteSpace,Button,Modal } from 'antd-mobile';
import axios from "axios";
import {connect} from "react-redux"
// import {get_mv} from "../../actions"
import Head from '../../components/head'
const alert = Modal.alert;
axios.defaults.baseURL = "http://47.98.135.91:4000/";
function test(str,msg){
    var reg = /^[a-zA-Z]{1}[A-Za-z0-9]{4,}$/;
    if(reg.test(str,msg)){
        return true;
    }else{
        const alertInstance = alert('提示', msg, [
            { text: '取消'},
            { text: '确定'},
          ]);
    }
}

@connect(
    state=>(
        {
        }
    )
)


export default class Register extends Component{
      showAlert = () => {
        var username= this.refs.one.value
        var userpwd= this.refs.two.value
        var queren= this.refs.three.value
        const {history}=this.props
       
        let count=0
        if(!test(username,'用户名必须是以字母开头，且5位数以上')){
            count++
        }else{
            if(!test(userpwd,'密码必须是以字母开头,且5位数以上')){
                count++
            }else{
                    if(queren!=userpwd){
                        count++
                        const alertInstance = alert('提示', '两次密码不相同', [
                            { text: '取消'},
                            { text: '确定'},
                          ]);
                    }
                }
            
        }
        if(count==0){
            axios.post('/react/register', { 
                username,
                userpwd,
                queren
             })
             .then(res=>{
                 if(res.data.code==0){
                    history.push('/login')   
                 }else{
                    const alertInstance = alert('提示', '用户名或密码错误', [
                        { text: '取消'},
                        { text: '确定'},
                      ]);
                 }
             })
        }
      };
      
    render(){
        console.log(this.props);
        const {dispatch,mv,history} = this.props; 
          
        return(
            <div>
                <Head title="注册" show="true" a="/login" name="登录"  history={history}/>
                <WhiteSpace size="lg" />
                <WingBlank>
                <div id="login">
                    <p>
                    <input type="text" ref='one' placeholder="用户名"/>
                    </p>
                    <WhiteSpace size="lg" />
                    <p>
                        <input type="text"  ref='two'  placeholder="密码" />
                    </p>
                         <WhiteSpace size="lg" />
                    <p>
                        <input type="text"  ref='three'  placeholder="确认密码" />
                    </p>
                    <WhiteSpace size="xl" />
                    <Button onClick={this.showAlert} type="primary" size="small" inline className="btn">注册</Button>
                </div>

                </WingBlank>
            </div>
        )
    }
}


