import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import { Carousel, WingBlank } from 'antd-mobile';
import {connect} from "react-redux"
import Foot from '../../components/foot'
import Head from '../../components/head'
import axios from "axios";
import {get_list,get_kindlist} from '../../actions'
axios.defaults.baseURL = "http://47.98.135.91:4000/";

@connect(
    state=>(
        {
            data:state.data,
            imgHeight:state.imgHeight,
            list:state.list
        }
    )
)

export default class Home extends Component{
    post_list_vegetables=()=>{
        var value=this.refs.one.alt
        const {dispatch} = this.props;
        dispatch(get_kindlist("/react/kindof",value))
        console.log(value)
    }
    post_list_fruits=()=>{
        var value=this.refs.two.alt
        const {dispatch} = this.props;
        dispatch(get_kindlist("/react/kindof",value))
        console.log(value)
    }

    componentWillMount(){
        const {dispatch,match} = this.props;
            dispatch(get_list("/react/list"))
        
       
    }
    render(){
        const {list,history} = this.props;

        return (
            <div>
                <Head title="海南农品商城" sousuo={true} history={history}/>
                <div id="home">
          <Carousel
            autoplay
            infinite
            dots={false}
            autoplayInterval={3000}
          >
            {this.props.data.map(val => (
              <a
                key={val}
                style={{ display: 'inline-block', width: '100%', height: this.props.imgHeight }}
              >
                <img
                  src={require( `../../../assets/images/banner${val}.jpg`)}
                  alt=""
                  style={{ width: '100%', verticalAlign: 'top' }}
                 
                />
              </a>
            ))}
          </Carousel>
                    <div>
                        <div className="nav_top">
                            <Link to='/app/shop'>
                                <img src={require('../../../assets/images/nav2.jpg')} alt=""/>
                                <p>招商加盟</p>
                            </Link>
                            <Link to='/app/shop'>
                                <img src={require('../../../assets/images/nav3.jpg')} alt=""/>
                                <p>农业技术</p>
                            </Link>
                            <Link to='/app/shop'>
                                <img src={require('../../../assets/images/nav4.jpg')} alt=""/>
                                <p>农博会</p>
                            </Link>
                            <Link to='/app/shop'>
                                <img src={require('../../../assets/images/nav5.jpg')} alt=""/>
                                <p>市场动态</p>
                            </Link>
                        </div>
                        <div className="nav_bottom">
                            <Link to='/app/'>
                                <img src={require('../../../assets/images/nav6.jpg')} alt=""/>
                                <p>农业图库</p>
                            </Link>
                            <Link to='/app/'>
                                <img src={require('../../../assets/images/nav7.jpg')} alt=""/>
                                <p>采购信息</p>
                            </Link>
                            <Link to='/app/'>
                                <img src={require('../../../assets/images/nav8.jpg')} alt=""/>
                                <p>人才招聘</p>
                            </Link>
                            <Link to='/app/'>
                                <img src={require('../../../assets/images/nav9.jpg')} alt=""/>
                                <p>联系我们</p>
                            </Link>
                        </div>

                    </div>
                    <div className='icon'>
                        <Link to='/app/shop/1'>
                            <img src={require('../../../assets/images/nav10.jpg')} ref='one' alt="水果" onClick={this.post_list_vegetables}/>
                        </Link>
                        <Link to='/app/shop/2'>
                            <img src={require('../../../assets/images/nav11.jpg')} ref='two' alt="蔬菜" onClick={this.post_list_fruits}/>
                        </Link>
                    </div>
                    <div className="list">

                        {
                            list.map((item,idx)=>(
                               

                                <Link key={idx} to={{
                                    pathname:"/app/shop1/detail/"+item.id+"/"+item.name,
                                    search:"?price="+item.jiage+"&icon1="+item.icon1+"&icon2="+item.icon2+"&icon3="+item.icon3
                                }}>
                                    <p >
                                        <img src={item.icon1} alt=""/>
                                        <b>{item.name}</b>
                                        <b>￥{item.jiage*1}</b>
                                    </p>
                                </Link>
                            ))
                        }
                    </div>
                </div>
                <div className="fenge">
                        --已经到底了--
                </div>
                <Foot/>
            </div>
        )
    }
}


