import React,{Component} from 'react';
import { List, InputItem, WingBlank,WhiteSpace,Button,Modal } from 'antd-mobile';
import axios from "axios";
import {connect} from "react-redux"
// import {get_mv} from "../../actions"
import Head from '../../components/head'
const alert = Modal.alert;
axios.defaults.baseURL = "http://47.98.135.91:4000/";
function test(str,msg){
    var reg = /^[a-zA-Z]{1}[A-Za-z0-9]{4,}$/;
    if(reg.test(str)){
        return true;
    }else{
        const alertInstance = alert('提示', '用户名或密码错误', [
            { text: '取消'},
            { text: '确定'},
          ]);
    }
}

@connect(
    state=>(
        {
        }
    )
)


export default class Login extends Component{
      showAlert = () => {
        var username= this.refs.one.value
        var userpwd= this.refs.two.value
       
        let count=0
        if(!test(username,'用户名必须是以字母开头，且5位数以上')){
            count++
        }else{
            if(!test(userpwd,'密码必须是以字母开头,且5位数以上')){
                count++
            }
        }
        if(count==0){
            axios.post('/react/login', { 
                username,
                userpwd
             })
             .then(res=>{
                 if(res.data.code==0){
                    localStorage.setItem('username', res.data.username);
                    const {history} = this.props;
                    history.push('/app/home')
                     console.log("成功")
                 }else{
                    const alertInstance = alert('提示', '用户名或密码错误', [
                        { text: '取消'},
                        { text: '确定'},
                      ]);
                 }
             })
        }
      };
      
    render(){
        console.log(this.props);
        const {dispatch,history} = this.props;
          
        return(
           
            <div>
                <Head title="账号登录" show="true" a="/register" name="注册"  history={history}/>
                <WhiteSpace size="lg" />
                <WingBlank>
                <div id="login">
                    <p>
                    <input type="text" ref='one' placeholder="用户名"/>
                    </p>
                    <WhiteSpace size="lg" />
                    <p>
                        <input type="text"  ref='two'  placeholder="密码" />
                    </p>
                    <WhiteSpace size="xl" />
                    <Button onClick={this.showAlert} type="primary" size="small" inline className="btn">登录</Button>
                </div>

                </WingBlank>
            </div>
        )
    }
}


