

const defaultState = {
    data: ['1', '2'],
    imgHeight: 'auto',
    list:[],
    carlist:[],
    value:1,
    count:0,
    priceStatus:1,
    salesStatus:1,
    nameStatus:1,
    checkboxCount:0,
    a:0,
    show:0,
}


export default (state=defaultState,action) => {
    switch(action.type){
   
        case "get_list_succ":
        return  {...state,...{list:action.data}}
        break;

        case "abc":
        return  {...state,...{a:state.a*1+action.num}}
        break;

        case "get_car":
        var carlist = [...action.data];
        return  {...state,...{carlist:carlist}}
        break;
        
        case "post_list_price":
        if(state.priceStatus==1){
            state.priceStatus=-1
        }else{
            state.priceStatus=1
        }
        return  {...state,...{list:action.data}}
        break;

        case "post_list_sales":
        if(state.salesStatus==1){
            state.salesStatus=-1
        }else{
            state.salesStatus=1
        }
        return  {...state,...{list:action.data}}
        break;

        case "post_list_name":
        if(state.nameStatus==1){
            state.nameStatus=-1
        }else{
            state.nameStatus=1
        }
        return  {...state,...{list:action.data}}
        break;

        case "addValue":
        return  {...state,...{value:state.value+action.num}}
        break;

        
        case "descValue":
        return  {...state,...{value:state.value-action.num}}
        break;
        
        case "updatenum":
        var carlist = [...action.data];
        return  {...state,...{carlist}};
        break;


        case "addColor":
        if(state.count>1){
            state.count=0
        }
        return  {...state,...{count:state.count+action.data}}
        break;
        

        case "showData":
        if(state.show>1){
            state.show=0
        }
        console.log(state.show)
        return  {...state,...{show:state.show+action.data}}
        break;

        case "changeTotal":
        console.log('11111111111111')
        console.log(state.a)
        return  {...state,...{a:action.num}}
        break;
        
        // case "onCheckbox":
        // console.log(state.checkboxCount)
        // if(state.count>1){
        //     state.count=0
        // }
        // return  {...state,...{checkboxCount:action.data}}
        // break;

        default:
        return state;
        break;
    }
}


