import React, { Component } from 'react';
// import {Link} from 'react-router-dom'
import { Carousel, WingBlank, WhiteSpace,Toast } from 'antd-mobile';
import Foot from '../../components/foot'
import Head from '../../components/head'
import { addValue, descValue, addColor } from '../../actions'
import { connect } from "react-redux"
import url from "url";
import axios from "axios";
axios.defaults.baseURL = "http://47.98.135.91:4000/";
function successToast() {
    Toast.success('加入购物车成功', 1);
  }
@connect(
    state => (
        {
            value: state.value,
            count: state.count
        }
    )
)
export default class Detail extends Component {
    addCount = () => {
        const { dispatch } = this.props;
        dispatch(addValue(1))
    }
    descCount = () => {
        const { dispatch } = this.props;
        var value = this.refs.num.value;
        if (value == 1) {
            return false
        }
        dispatch(descValue(1))
    }
    addColor = () => {
        const { dispatch, count } = this.props;
        dispatch(addColor(1))
        const collect = this.refs.one
        if (count == 1) {
            collect.style.color = '#000'
        } else {
            collect.style.color = 'red'
        }
    }
    addShopCar=()=>{
        const { match, history, location } = this.props;
        const id=match.params.id
        const name=match.params.name
        const num=this.refs.num.value;
        const price=url.parse(location.search, true).query.price
        const icon=url.parse(location.search, true).query.icon1
        const username=localStorage.getItem('username')
        console.log(num)
        console.log(name)
        console.log(id)
        console.log(price)
        axios.post('react/car',{
            id,
            name,
            num,
            price,
            icon,
            username
        })
        successToast()
    }


    render() {
        console.log(this.props)
        const { match, history, location, value, dispatch, count } = this.props;
        return (
            <div id="detail">
            <Head title={match.params.name} show={true} a='' history={history}/>
                <Carousel
                    autoplay={false}
                    infinite
                >
                    <div className="icon">
                        <img
                            src={url.parse(location.search, true).query.icon2}
                            alt=""
                            onLoad={() => {
                                window.dispatchEvent(new Event('resize'));
                                { imgHeight: 'auto' };
                            }}
                        />
                    </div>
                    <div className="icon">
                        <img
                            src={url.parse(location.search, true).query.icon1}
                            alt=""
                            onLoad={() => {
                                window.dispatchEvent(new Event('resize'));
                                { imgHeight: 'auto' };
                            }}
                        />
                    </div>
                    <div className="icon">
                        <img
                            src={url.parse(location.search, true).query.icon3}
                            alt=""
                            onLoad={() => {
                                window.dispatchEvent(new Event('resize'));
                                { imgHeight: 'auto' };
                            }}
                        />
                    </div>
                </Carousel>
                <WhiteSpace size="lg" />
                <p>{match.params.name}</p>
                <WhiteSpace size="lg" />
                <p ref='price'>￥{url.parse(location.search, true).query.price}</p>
                <WhiteSpace size="lg" />
                <div className="number">
                    <div>
                        数量:
                    </div>
                    <div>
                        <span onClick={this.descCount}>-</span>
                        <input type="text" value={value} ref='num' onChange={() => { }} />
                        <span onClick={this.addCount}>+</span>
                    </div>
                </div>
                <div className="nav">
                    
                    <ul>
                        <hr />
                        <li>
                            <WingBlank>
                                <b>领券</b>
                                <img src={require('../../../assets/images/quan.jpg')} alt="" />
                            </WingBlank>
                        </li>
                        <hr />
                        <li>
                            <WingBlank>
                                <b>促销</b>
                                <span>此价格不与套餐优惠同时享受</span>
                            </WingBlank>
                        </li>
                        <hr />
                        <li>
                            <WingBlank>
                                <b>服务</b>
                                <span>货到付款</span>
                                <span>准时达</span>
                                <span>不支持7天无理由退货</span>
                            </WingBlank>
                        </li>
                    </ul>
                </div>
                <div className="fenge">

                </div>
                <footer>
                    <div className="collect"  ref='one'>
                        <span><i className="iconfont icon-guanzhu" onClick={this.addColor}></i></span>
                        <p>收藏</p>
                    </div>
                    <div className="confirm">
                        <p onClick={this.addShopCar}>加入购物车</p>
                    </div>
                </footer>
            </div>
        )
    }
}


