import React,{Component} from 'react';
import {Link} from 'react-router-dom'

import Foot from '../../components/foot'
import Head from '../../components/head'

export default class Home extends Component{
    state={
        flag:false,
    }
    exit=()=>{
        localStorage.removeItem('username');
        this.setState({
            flag:false,
        })
    }
    componentWillMount(){
        if(localStorage.getItem('username')){
            var username=localStorage.getItem('username')
            this.setState({
                flag:true,
            })
        }else{
            this.setState({
                flag:false,
            })
        }
    }
    render(){
        const username=localStorage.getItem("username");
         console.log(this.state.flag);
        return (
            <div id="my">
               <div>我的</div>  
                <Link ref='login' to='/login' style={{display:this.state.flag?"none":"block",color:'red'}}>登陆</Link> 
                <p ref='username' style={{display:this.state.flag?"block":"none"}}>{username}</p>  
                <h1 onClick={this.exit} style={{display:this.state.flag?"block":"none"}}>退出</h1>
                <Foot/>
            </div>
        )
    }
}


