import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Carousel, WingBlank, NavBar, Icon } from 'antd-mobile';
import { connect } from "react-redux"
import Foot from '../../components/foot'
import Head from '../../components/head'
import axios from "axios";

import { get_list, post_list, post_list_price,post_list_sales,post_list_name } from '../../actions'
axios.defaults.baseURL = "http://47.98.135.91:4000/";

@connect(
    state => (
        {
            data: state.data,
            imgHeight: state.imgHeight,
            list: state.list,
            priceStatus: state.priceStatus,
            salesStatus: state.salesStatus,
            nameStatus:state.nameStatus
        }
    )
)

export default class Home extends Component {
    goback = () => {
        const { history } = this.props;
        history.goBack()
    }
    getValue = () => {
        const value = this.refs.value.value;
        console.log(value)
        const { dispatch } = this.props;
        dispatch(post_list("/react/list", value))
    }
    changeSort = () => {
        const value = this.refs.value.value;
        const { dispatch, priceStatus } = this.props;
        const priceOne = this.refs.priceOne
        const priceTwo = this.refs.priceTwo
        if (priceStatus == -1) {
            priceOne.style.color = 'red'
            priceTwo.style.color = '#999'
        } else {
            priceTwo.style.color = 'red'
            priceOne.style.color = '#999'
        }
        console.log(priceStatus)
        dispatch(post_list_price("/react/listPrice", value, priceStatus))
    }
    changeSalesSort = () => {
        const value = this.refs.value.value;
        const { dispatch, salesStatus } = this.props;
        const salesOne = this.refs.salesOne
        const salesTwo = this.refs.salesTwo
        if (salesStatus == -1) {
            salesOne.style.color = 'red'
            salesTwo.style.color = '#999'
       
        } else {
            salesTwo.style.color = 'red'
            salesOne.style.color = '#999'
  
        }
        console.log(salesStatus)
        dispatch(post_list_sales("/react/listsales", value, salesStatus))
    }


    changeNameSort = () => {
        const value = this.refs.value.value;
        const { dispatch, nameStatus } = this.props;
        const nameOne = this.refs.nameOne
        const nameTwo = this.refs.nameTwo
        if (nameStatus == -1) {
            nameOne.style.color = 'red'
            nameTwo.style.color = '#999'
        } else {
            nameTwo.style.color = 'red'
            nameOne.style.color = '#999'
        }

        dispatch(post_list_name("/react/listname", value, nameStatus))
    }

    componentWillMount() {
        const { dispatch, match } = this.props;
        var flag;
        if (match.params.judg) {
            flag = false;
        } else {
            flag = true;
        }
        if (flag) {
            dispatch(get_list("/react/list"))
        }
    }

    render() {
        const { list, history } = this.props;

        return (
            <div id="shop">
                <NavBar
                    className="head"
                    mode="dark"
                    icon={<Icon type="left" onClick={this.goback} />}

                    rightContent={
                        <Icon key="0" type="search" onClick={this.getValue} style={{ marginRight: '16px' }} />
                    }
                ><input type="text" ref='value' /></NavBar>
                <div className="sort">
                    <b onClick={this.changeNameSort}>综合
                        <span>
                            <i className="iconfont icon-arrow-up" ref='nameOne'></i>
                            <i className="iconfont icon-arrow-down" ref='nameTwo'></i>
                        </span>
                    </b>
                    <b onClick={this.changeSalesSort}>销量
                         <span>
                            <i className="iconfont icon-arrow-up" ref='salesOne'></i>
                            <i className="iconfont icon-arrow-down" ref='salesTwo'></i>
                        </span>
                    </b>
                    <b onClick={this.changeSort}>价格
                         <span>
                            <i className="iconfont icon-arrow-up" ref='priceOne'></i>
                            <i className="iconfont icon-arrow-down" ref='priceTwo'></i>
                        </span>
                    </b>
                </div>
                <div className="list">
                    {
                        list.map((item, idx) => (
                            <Link key={idx} to={{
                                pathname: "/app/shop1/detail/" + item.id + "/" + item.name,
                                search: "?price=" + item.jiage + "&icon1=" + item.icon1 + "&icon2=" + item.icon2 + "&icon3=" + item.icon3
                            }}>
                                <p >
                                    <img src={item.icon1} alt="" />
                                    <b>{item.name}</b>
                                    <b>￥{item.jiage * 1}</b>
                                </p>
                            </Link>
                        ))
                    }
                </div>
                <div className="fenge" style={{ height: '20px' }}>
                    --已经到底了--
                </div>
            </div>
        )
    }
}


