
import React,{Component} from "react";
import {NavBar,Icon} from "antd-mobile";
import {Link} from 'react-router-dom'

export default class Head extends Component{
  
    goback=()=>{
        const {history } = this.props;
        history.goBack();
    }

    render(){
        const {title,show,sousuo,a,name,history} = this.props;
        console.log(history);
        return (
            <NavBar
            mode="dark"
            icon={show?<Icon type="left" />:""}
            onLeftClick={show?this.goback:console.log()}
           
            rightContent={sousuo?[
              <Icon key="0" type="search" onClick={()=>{history.push('/app/shop');}} style={{ marginRight: '16px' }}/>,
            ]:<Link to={a}>{name}</Link>}
          >{title}</NavBar>
        )
    }
}


